/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/* Front page */
.home-hero {
  height: 100vh; }

.home-hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url("../../images/home-hero.jpg");
  background-size: cover;
  width: 100%;
  height: 100%; }

.home-shop-flex-wrapper {
  display: flex;
  justify-content: space-between; }
  .home-shop-flex-wrapper .shop-category {
    width: 23%;
    padding: 1.5rem; }
    .home-shop-flex-wrapper .shop-category:hover {
      border-color: #d99054; }
    .home-shop-flex-wrapper .shop-category img {
      width: 75px; }

.home-journal-title-wrapper {
  position: relative;
  height: 175px;
  padding: 15px; }

.home-journal-flex-wrapper {
  display: flex;
  justify-content: space-between; }
  .home-journal-flex-wrapper article {
    width: 32%;
    text-align: left; }
    .home-journal-flex-wrapper article .home-journal-image-wrapper {
      width: 100%;
      height: 15vw;
      overflow: hidden; }

/* pages without hero image*/
body:not(.home):not(.page-template-about) .site-content {
  padding-top: 50px; }

/* Pages with sidebar area */
body:not(.home):not(.page-template-about):not(.single-product):not(.post-type-archive):not(.single-adventure) .site-content {
  display: flex;
  justify-content: space-between;
  max-width: 1120px;
  margin: 0 auto; }

body:not(.home):not(.page-template-about):not(.single-product):not(.post-type-archive):not(.single-adventure) .content-area {
  width: 60%; }

.widget-area {
  width: 30%; }

/* About Page */
.page-template-about .entry-header {
  color: #fff;
  height: 100vh; }
  .page-template-about .entry-header .entry-title {
    font-size: 3em;
    font-weight: bold; }

.about-flex-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.page-template-about .entry-content h2 {
  text-align: center; }

/* Product Archive */
body.tax-product-type .product-type-block-wrapper, body.post-type-archive-product .product-type-block-wrapper {
  width: 10%; }

body.tax-product-type .product-type-flex-wrapper, body.post-type-archive-product .product-type-flex-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 80em;
  max-width: 1120px; }
  body.tax-product-type .product-type-flex-wrapper article.type-product, body.post-type-archive-product .product-type-flex-wrapper article.type-product {
    flex-basis: 23%;
    max-width: 23%; }
    body.tax-product-type .product-type-flex-wrapper article.type-product .product-image-wrapper, body.post-type-archive-product .product-type-flex-wrapper article.type-product .product-image-wrapper {
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 15vw; }
      body.tax-product-type .product-type-flex-wrapper article.type-product .product-image-wrapper img, body.post-type-archive-product .product-type-flex-wrapper article.type-product .product-image-wrapper img {
        max-height: 250px;
        max-width: none;
        width: auto; }

.dots {
  width: 100%;
  z-index: -1;
  position: absolute; }

.product-name, .product-price {
  background: #fff;
  z-index: 10; }

.product-meta {
  overflow: hidden;
  flex-wrap: none;
  display: flex;
  justify-content: space-between;
  padding: 10px; }

.product-type-links {
  text-align: center;
  justify-content: center; }
  .product-type-links a {
    text-decoration: none; }

/* Single Product Page */
body.single-product article.type-product {
  padding: 2.5rem 0 4rem 0;
  display: flex; }

body.single-product .entry-content {
  width: 50%;
  margin: 0;
  padding: 0 2rem; }

body.single-product .single-product-title {
  margin-top: 0; }

body.single-product .single-product-price {
  padding-bottom: 1rem;
  font-size: 1.5rem; }

.site-footer {
  background: url("../../images/dark-wood.png");
  height: 350px;
  color: #fff; }
  .site-footer a {
    text-decoration: none;
    color: #fff; }
  .site-footer a:hover {
    text-decoration: underline; }

.footer-container {
  display: flex;
  justify-content: space-between; }

.footer-content-container {
  padding: 60px 60px 30px 0; }

.footer-image-container {
  flex-grow: 2;
  display: flex;
  justify-content: flex-end;
  align-self: flex-start;
  padding-top: 60px; }

.footer-image {
  background: url("../../images/inhabitent-logo-text.svg") no-repeat;
  background-size: contain;
  height: 100px;
  width: 300px; }

i {
  padding-right: 5px; }

.social-icons i {
  padding-right: 10px; }
